import {useCallback, useEffect, useState} from "react";
import {TileDto} from "../../dtos/game/tileDto";
import {CoordinateDto} from "../../dtos/game/coordinateDto";
import Container from "../../containers/container";
import {TurnDto} from "../../dtos/game/turnDto";

export function useGame() {
    const tileService = Container.getTileService();
    const boardService = Container.getBoardService();
    const testEndService = Container.getTestGameService();
    const [board, setBoard] = useState<TileDto[][]>([]);
    const [score, setScore] = useState(0);
    const [touchStart, setTouchStart] = useState<CoordinateDto | null>();
    const [touchEnd, setTouchEnd] = useState<CoordinateDto | null>();

    const setGame = useCallback(() => {
        let newBoard = boardService.generateBoard();
        setBoard(newBoard);
    }, []);

    useEffect(() => {
        setGame();
    }, [setGame]);


    const handleSwipe = useCallback(() => {
        if (!touchStart || !touchEnd) return;
        const distanceX = touchStart.x - touchEnd.x;
        const distanceY = touchStart.y - touchEnd.y;
        const isHorizontal = Math.abs(distanceX) > Math.abs(distanceY);

        let newBoard: TileDto[][] | null = null;
        if (isHorizontal) {
            if (distanceX > 20) {
                newBoard = tileService.slideTo(TurnDto.LEFT);
            } else if (distanceX < -20) {
                newBoard = tileService.slideTo(TurnDto.RIGHT);
            }
        } else {
            if (distanceY > 20) {
                newBoard = tileService.slideTo(TurnDto.UP);
            } else if (distanceY < -20) {
                newBoard = tileService.slideTo(TurnDto.DOWN);
            }
        }
        if (newBoard) {
            setScore(boardService.getScore())
            setBoard(boardService.getCopyBoard() as TileDto[][]);

            setTimeout(() => {
                newBoard = boardService.generateTile();
                setBoard(newBoard);
                setTimeout(() => {
                    boardService.removeIsNew();
                    setBoard(boardService.getCopyBoard());
                }, 400)
            }, 300)

            testEndService.testEndGame();
        }
    }, [board, touchStart, touchEnd]);


    const onTouchStart = (e: any) => {
        setTouchEnd(null);
        setTouchStart({
            x: e.targetTouches[0].clientX,
            y: e.targetTouches[0].clientY
        });
    };

    const onTouchMove = (e: any) => {
        setTouchEnd({
            x: e.targetTouches[0].clientX,
            y: e.targetTouches[0].clientY
        });
    };

    const onTouchEnd = () => {
        handleSwipe();
        setTouchStart(null);
        setTouchEnd(null);
    };

    const handleKeyDown = useCallback((event: any) => {
        let newBoard: TileDto[][];
        switch (event.code) {
            case 'ArrowLeft':
                newBoard = tileService.slideTo(TurnDto.LEFT);
                break;
            case 'ArrowRight':
                newBoard = tileService.slideTo(TurnDto.RIGHT);
                break;
            case 'ArrowUp':
                newBoard = tileService.slideTo(TurnDto.UP);
                break;
            case 'ArrowDown':
                newBoard = tileService.slideTo(TurnDto.DOWN);
                break;
            default:
                return;
        }
        setScore(boardService.getScore())
        setBoard(newBoard);
        setTimeout(() => {
            newBoard = boardService.generateTile();
            setBoard(newBoard);
            setTimeout(() => {
                boardService.removeIsNew();
                setBoard(boardService.getCopyBoard());
            }, 400)
        }, 300)
        testEndService.testEndGame();
    }, [board]);

    useEffect(() => {
        window.addEventListener('keydown', handleKeyDown);
        return () => {
            window.removeEventListener('keydown', handleKeyDown);
        };
    }, [handleKeyDown]);
    return {
        onTouchStart: onTouchStart,
        onTouchMove: onTouchMove,
        onTouchEnd: onTouchEnd,
        score: score,
        board: board
    }
}